import React, { Component, PropTypes } from 'react';
import { Icon } from 'antd-mobile';

import Backbar from '../../module/backbar/backbar';
import ViewList from '../../widget/viewList/viewList';
import lazyloader from '../../widget/lazyLoad/lazyloader';

//导入样式 start
import './consumptionInfo.scss'
//导入样式 end



class ConsumptionInfo extends Component{
    constructor(){
        super();
        this.state = {
            consumeDetails : {
                consume : {},
                consumeDetailsList : []
            },
            type : Utils.Url.parseUrl(location.hash).params.type    //1 消费  2 退款
        }
    }
    componentWillMount(){

        //在此处初始化状态
        var that = this;
      
        Utils.tokenExpireJumpToLogin(function () {
            
            //获取本金消费详情
            Utils.requestData({
                url: config.api +"qjlianlian/front/v1/principalConsume/getConsumeDetails",
                method: 'post',
                data: {
                    consumeId : Utils.Url.parseUrl(location.hash).params.consumeId
                },
                callback: function(data){
             
                    if(data.resultCode=="0"){

                        that.state.consumeDetails.consume = data.data.consume;
                        that.state.consumeDetails.consumeDetailsList = data.data.consumeDetailsList;

                        componentStore.update(that,{
                            consumeDetails: that.state.consumeDetails
                        })

                    }
                    
                }
            });
            
        })

    }

    componentDidMount(){
        var that = this;
        // 存储 start
        componentStore.set(this);
        // 存储 end




    };
    componentWillUnmount(){
        // 清除 start
        componentStore.clear(this);
        // 清除 end
    };

    
   

    render(){
        var that = this;
        var type = that.state.type;
        return (

            <div>
                    
                {(()=>{
                    if(type==1 || type==2){
                        return (
                            <Backbar $id="consumptionInfo" title="消费详情">
                            
                                <div className="consumptionInfo-wrap">
                                    <div className="g-bill-detail">
                                        <ul className="g-d-list">
                                            <li>
                                                {(()=>{
                                                    if(type==1){    //消费
                                                        return (
                                                            <h4>支付金额：</h4>
                                                        )
                                                    }else if(type==2){ //退款
                                                        return (
                                                            <h4>退款金额：</h4>
                                                        )
                                                    }
                                                })()}
                                                
                                                <p><b>￥</b>{that.state.consumeDetails.consume.vAmount}</p>
                                            </li>
                                        </ul>
                                        <ul className="g-d-list">
                                            <li>
                                                <h4>商户名称</h4>
                                                <div>
                                                    <p>{that.state.consumeDetails.consume.pMerchantName}</p>
                                                </div>
                                            </li>
                                            <li>
                                                <h4>交易时间</h4>
                                                <div>
                                                    <p>{that.state.consumeDetails.consume.transactionTime}</p>
                                                </div>
                                            </li>
                                            <li>
                                                <h4>消费订单号</h4>
                                                <div>
                                                    <p>{that.state.consumeDetails.consume.pOid}</p>
                                                </div>
                                            </li>
                                            {(()=>{
                                                if(that.state.consumeDetails.consumeDetailsList.length>0){
                                                    return (
                                                        <li>
                                                            <h4>本金订单号</h4>
                                                            <div>
                                                                {(()=>{
                            
                                                                    return that.state.consumeDetails.consumeDetailsList.map(function(item,index){
                                                                        return(
                                                                            <p key={index}>{item.oid}</p>
                                                                        )
                                                                    })
                            
                                                                })()}
                            
                                                            </div>
                                                        </li>
                                                    )
                                                }
                                            })()}
                                            
                                        </ul>
                                        
                                    </div>
                                </div>

                            </Backbar>
                        )
                    }else if(type==3 || type==4){
                        return (
                            <Backbar $id="consumptionInfo" title="账单详情">
                            
                                <div className="consumptionInfo-wrap">
                                    <div className="g-bill-detail">
                                        <ul className="g-d-list">
                                            <li>
                                                {(()=>{
                                                    if(type==3){    //投资本金
                                                        return (
                                                            <h4>投资本金：</h4>
                                                        )
                                                    }else if(type==4){ //冻结本金                                                    
                                                        return (
                                                            <h4>冻结本金：</h4>
                                                        )
                                                    }
                                                })()}
                                                
                                                <p><b>￥</b>{that.state.consumeDetails.consume.vAmount}</p>
                                            </li>
                                        </ul>
                                        <ul className="g-d-list">
                                            <li>
                                                <h4>订单号</h4>
                                                <div>
                                                    <p>{that.state.consumeDetails.consumeDetailsList.length>0?that.state.consumeDetails.consumeDetailsList[0].oid:""}</p>
                                                </div>
                                            </li>
                                            <li>
                                                <h4>项目名称</h4>
                                                <div>
                                                    <p>{that.state.consumeDetails.consume.pMerchantName}</p>
                                                </div>
                                            </li>
                                            <li>
                                                <h4>投资日期</h4>
                                                <div>
                                                    <p>{that.state.consumeDetails.consume.transactionTime}</p>
                                                </div>
                                            </li>
                                            <li>
                                                <h4>投资金额</h4>
                                                <div>
                                                    {(()=>{
                                                        if(type==3){    //投资本金
                                                            return (
                                                                <p>{that.state.consumeDetails.consume.vAmount}元</p>
                                                            )
                                                        }else if(type==4){ //冻结本金                                                    
                                                            return (
                                                                <p>{that.state.consumeDetails.consume.investAmount}元</p>
                                                            )
                                                        }
                                                    })()}
                                                    
                                                </div>
                                            </li>
                                            
                                            
                                        </ul>
                                        
                                    </div>
                                </div>

                            </Backbar>
                        )
                    }
                })()}
            </div>

        )
    }

}

export default ConsumptionInfo;



